const CarouselItem = (props) => 
{
    const { imgObj } = props;
        return (
            <div class={ imgObj.active ? "carousel-item  active" : "carousel-item" }>
                <img class="d-block w-100 " src={imgObj.src} alt="First slide" />
            </div>
        );
}
export default CarouselInner;